body {
    padding: 0;
    font-family: sans-serif;
  }
  .login,
  .image {
    min-height: 100vh;
  }
  
  .bg-image {
    background-image: url("https://res.cloudinary.com/mhmd/image/upload/v1555917661/art-colorful-contemporary-2047905_dxtao7.jpg");
    background-size: cover;
    background-position: center center;
  }
  
  .btn-mixed {
    background: rgb(131, 58, 180);
    background: linear-gradient(
      90deg,
      rgba(131, 58, 180, 1) 0%,
      rgba(253, 29, 29, 1) 59%,
      rgba(252, 176, 69, 1) 100%
    );
  }
  
  #avatar {
    background-size: contain;
    width: 200px;
    height: auto;
    margin-left: 35%;
  }
  
  #login {
    color: rgb(131, 58, 180);
    cursor:pointer
  }

  #error {
      color:red
  }
  
  @media (max-width: 1280px) {
    #avatar {
      background-size: contain;
      width: 200px;
      height: auto;
      margin-left: 23%;
    }
  
    .regform {
      height: 61%;
      margin: -113px;
      margin-left: -148px;
    }
  }
  
  @media (max-width: 760px) {
    .regform {
      height: 92%;
      margin-left: -107px;
    }
    .textbox {
      width: 70%;
    }
    .regform h1 {
      text-align: inherit;
    }
    s .close {
      top: -25px;
      width: 616px;
    }
  }
  
