  /* Pop Up */
  
  .textbox {
    font-size: 20px;
    padding: 8px 0;
    margin: 8px 0;
    border-bottom: 1px;
    text-align: center;
    color: orangered;
  }
  
  .textbox i {
    width: 26px;
    float: left;
  }
  
  .textbox input {
    border: none;
    outline: none;
    background: none;
    font-size: 18px;
    width: 80%;
    margin: 0 10px;
    text-align: center;
    background-color: transparent;
    border-style: none;
  }
  
  .textbox label {
    float: left;
  }
  
  ::placeholder {
    color: white;
    opacity: 0.7;
  }
  
