  #profession {
    margin-left: -24px;
  }
  
  .myButton {
    box-shadow: 3px 4px 0px 0px #8a2a21;
    background: linear-gradient(to bottom, #3420e6 5%, #bcdfe8 100%);
    background-color: #3420e6;
    border-radius: 30px;
    border: 1px solid #2ee88b;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 17px;
    padding: 7px 25px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #810e05;
    margin: 12px;
  }
  .myButton:hover {
    background: linear-gradient(to bottom, #bcdfe8 5%, #3420e6 100%);
    background-color: #bcdfe8;
  }
  .myButton:active {
    position: relative;
    top: 1px;
  }
  
  .close {
    position: absolute;
    margin-top: -151px;
    right: -18px;
    color: red;
    height: 20px;
    border-radius: 50%;
    cursor: pointer;
  }
  